export default {
	data() {
		return {
			show: false,
			product: {},
			cause: [{
					'value': '错拍/多拍/不想要',
					'checked': true
				},
				{
					'value': '协商一致退款',
					'checked': false
				},
				{
					'value': '未按约定时间发货',
					'checked': false
				},
				{
					'value': '其他',
					'checked': false
				}
			],
			cause1: 0,
			tempFilePaths:[]
		}
	},
	methods: {
		choose() {
			this.show = true
		},
		onClose() {
			this.show = false
		},
		// 原因选择
		radioChange(e) {
			this.cause1 = this.cause[e.currentTarget.dataset.index].value
			// console.log(this.cause1)
		},
		voucher() {
			var that=this
			wx.chooseImage({
				count: 3,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					// tempFilePath可以作为img标签的src属性显示图片
					that.tempFilePaths = res.tempFilePaths
					console.log(that.tempFilePaths)
				}
			})
		}
	},
	onLoad(options) {
		this.product = JSON.parse(options.item)
	}
}
